@mixin control {
  -moz-appearance: none;
  -webkit-appearance: none;
  align-items: center;
  background: $control-background;
  border: 1px solid $control-border;
  border-radius: $radius;
  color: $control;
  display: inline-flex;
  font-size: $size-normal;
  height: 32px;
  line-height: 24px;
  padding: 3px 8px;
  position: relative;
  vertical-align: top;
  &:hover {
    border-color: $control-hover-border; }
  &:active,
  &:focus {
    border-color: $control-active-border;
    outline: none; }
  &[disabled] {
    &,
    &:hover {
      background: $background;
      border-color: $control-border;
      cursor: not-allowed;
      @include placeholder {
        color: rgba($control, 0.3); } } } }

@mixin form-control {
  @include control;
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    &.is-#{$name} {
      border-color: $color; } } }

@mixin control-small {
  border-radius: 2px;
  font-size: 11px;
  height: 24px;
  line-height: 16px;
  padding: 3px 6px; }
@mixin control-medium {
  font-size: 18px;
  height: 40px;
  line-height: 32px;
  padding: 3px 10px; }
@mixin control-large {
  font-size: 24px;
  height: 48px;
  line-height: 40px;
  padding: 3px 12px; }

.input {
  @include form-control;
  box-shadow: inset 0 1px 2px rgba(black, 0.1);
  display: block;
  max-width: 100%;
  width: 100%;
  &[type="search"] {
    border-radius: 290486px; }
  &.is-flat {
    border: none;
    box-shadow: none;
    padding: 4px 8px; }
  &.is-small {
    @include control-small;
    &.is-flat {
      padding: 4px 6px; } }
  &.is-medium {
    @include control-medium;
    &.is-flat {
      padding: 4px 10px; } }
  &.is-large {
    @include control-large;
    &.is-flat {
      padding: 4px 12px; } }
  &.is-fullwidth {
    display: block;
    width: 100%; }
  &.is-inline {
    display: inline;
    width: auto; } }

.textarea {
  @extend .input;
  line-height: 1.2;
  max-height: 600px;
  max-width: 100%;
  min-height: 120px;
  min-width: 100%;
  padding: 10px;
  resize: vertical; }

%control-with-element {
  cursor: pointer;
  display: inline-block;
  line-height: 16px;
  padding-left: 18px;
  position: relative;
  vertical-align: top;
  input {
    @include form-control;
    border-radius: 1px;
    box-shadow: inset 0 1px 1px rgba(black, 0.1);
    cursor: pointer;
    float: left;
    height: 14px;
    left: 0;
    outline: none;
    padding: 0;
    position: absolute;
    top: 1px;
    width: 14px;
    &:after {
      @include arrow($control-active-background-invert);
      height: 4px;
      left: 3px;
      opacity: 0;
      position: absolute;
      top: 3px;
      transform: rotate(-45deg) scale(1); }
    &:checked {
      background: $control-active-background;
      border-color: $control-active-background;
      box-shadow: none;
      &:after {
        opacity: 1; } } }
  &:hover {
    color: $control-hover;
    input {
      border-color: $control-hover-border;
      &:checked {
        border-color: $control-active-border; } } }
  &.is-disabled {
    &,
    &:hover {
      color: $text-light; } } }

.checkbox {
  @extend %control-with-element; }

.radio {
  @extend %control-with-element;
  & + .radio {
    margin-left: 10px; }
  input {
    border-radius: 8px;
    &:after {
      background: $link-invert;
      border: 0;
      border-radius: 2px;
      left: 4px;
      top: 4px;
      transform: none;
      width: 4px; } } }

.select {
  display: inline-block;
  height: 32px;
  position: relative;
  vertical-align: top;
  select {
    @include form-control;
    cursor: pointer;
    display: block;
    outline: none;
    padding-right: 36px;
    &:hover {
      border-color: $control-hover-border; }
    &::ms-expand {
      display: none; } }
  &.is-fullwidth {
    width: 100%;
    select {
      width: 100%; } }
  &:after {
    @include arrow($link);
    margin-top: -6px;
    right: 16px;
    top: 50%; }
  &:hover {
    &:after {
      border-color: $link-hover; } } }

.label {
  color: $text-strong;
  display: block;
  font-weight: bold;
  &:not(:last-child) {
    margin-bottom: 5px; } }

.help {
  display: block;
  font-size: $size-small;
  margin-top: 5px;
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    &.is-#{$name} {
      color: $color; } } }

// Containers

.control-label {
  @include mobile {
    margin-bottom: 5px; }
  @include tablet {
    flex: 1;
    margin-right: 20px;
    padding-top: 7px;
    text-align: right; } }

.control {
  position: relative;
  text-align: left;
  &.is-loading {
    &:after {
      @extend .loader;
      position: absolute !important;
      right: 8px;
      top: 8px; } }
  &:not(:last-child) {
    margin-bottom: 10px; }
  &.has-icon {
    & > .fa {
      @include fa(14px, 16px);
      color: $text-light;
      pointer-events: none;
      position: absolute;
      top: 8px;
      z-index: 4; }
    .input {
      &:focus + .fa {
        color: $text-strong; } }
    &:not(.has-icon-right) {
      & > .fa {
        left: 8px; }
      .input {
        padding-left: 32px; } } }
  &.has-icon-right {
    & > .fa {
      right: 8px; }
    .input {
      padding-right: 32px; } }
  &.has-addons {
    display: flex;
    .input,
    .button,
    .select {
      border-radius: 0;
      margin-right: -1px;
      &:hover {
        z-index: 2; }
      &:active,
      &:focus {
        z-index: 3; }
      &:first-child {
        border-radius: $radius 0 0 $radius;
        select {
          border-radius: $radius 0 0 $radius; } }
      &:last-child {
        border-radius: 0 $radius $radius 0; } }
    &.is-centered {
      justify-content: center; } }
  &.is-grouped {
    display: flex;
    & > .button,
    & > .input,
    & > .select {
      &:not(:last-child) {
        margin-right: 10px; } }
    & > .input {
      flex: 1; } }
  &.is-horizontal {
    @include tablet {
      display: flex;
      & > .control {
        display: flex;
        flex: 5; } } } }
