.content {
  @extend .block;
  &.is-medium {
    font-size: $size-5;
    code {
      font-size: $size-6; } }
  &.is-large {
    font-size: $size-4;
    code {
      font-size: $size-5; } }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: $text-strong;
    font-weight: 300;
    line-height: 1.125;
    margin-bottom: 20px; }
  h1,
  h2,
  h3 {
    &:not(:first-child) {
      margin-top: 40px; } }
  h1 {
    font-size: 2em; }
  h2 {
    font-size: 1.75em; }
  h3 {
    font-size: 1.5em; }
  h4 {
    font-size: 1.25em; }
  h5 {
    font-size: 1.125em; }
  h6 {
    font-size: 1em; }
  p:not(:last-child) {
    margin-bottom: 1em; }
  li + li {
    margin-top: 0.25em; }
  ol {
    list-style: decimal outside;
    margin: 1em 2em; }
  ul {
    list-style: disc outside;
    margin: 1em 2em;
    ul {
      list-style-type: circle;
      margin-top: 0.5em;
      ul {
        list-style-type: square; } } }
  blockquote {
    background: $background;
    border-left: 5px solid $border;
    padding: 1.5em;
    &:not(:last-child) {
      margin-bottom: 1em; } } }
